:root {
  --gold: #d4af37;
  --gold-soft: #f5d76e;

  --green: #1f6f43;
  --green-soft: #2fa56f;

  --bg-dark: #020617;
  --bg-card: rgba(15, 23, 42, 0.9);
  --border-dark: #334155;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* ===== Page ===== */
.app-bg {
  min-height: 100vh;
  background: radial-gradient(circle at top, #1e293b, #020617);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  color: #e5e7eb;
  font-family: system-ui, -apple-system, sans-serif;
}

/* ===== Card ===== */
.game-card {
  width: 100%;
  max-width: 80rem;
  background: var(--bg-card);
  backdrop-filter: blur(10px);
  border-radius: 1.25rem;
  padding: 2rem;
  border: 1px solid var(--gold);
  box-shadow: 0 25px 60px rgba(0, 0, 0, 0.6);
}

/* ===== Logo ===== */
.logo-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 0.75rem;
}

.vipdle-logo {
  max-width: 420px;
  width: 100%;
  height: auto;
}

.subtitle {
  text-align: center;
  color: var(--gold-soft);
  margin-bottom: 2rem;
}

/* ===== Input ===== */
.guess-form {
  display: flex;
  gap: 0.75rem;
  justify-content: center;
  margin-bottom: 2rem;
}

.guess-input {
  width: 16rem;
  max-width: 100%;
  padding: 0.75rem 1rem;
  border-radius: 0.75rem;
  background: var(--bg-dark);
  border: 1px solid #475569;
  color: #e5e7eb;
}

.guess-input:focus {
  outline: none;
  border-color: var(--gold);
  box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.4);
}

.guess-btn {
  padding: 0.75rem 1.25rem;
  border-radius: 0.75rem;
  background: linear-gradient(135deg, var(--gold), var(--gold-soft));
  color: #020617;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.guess-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* ===== Win Banner ===== */
.win-banner {
  margin-bottom: 1.5rem;
  padding: 1.25rem;
  text-align: center;
  border-radius: 0.75rem;
  border: 1px solid var(--gold);
  background: linear-gradient(
    135deg,
    rgba(212, 175, 55, 0.15),
    rgba(31, 111, 67, 0.15)
  );
  color: var(--gold-soft);
  font-size: 1.5rem;
  font-weight: 700;
}

/* ===== Table ===== */
.table-wrapper {
  width: 100%;
  overflow-x: visible;
  overflow-y: visible;
}

.table {
  min-width: 1024px;
  overflow: visible;
}

.table-header,
.table-row {
  display: grid;
  grid-template-columns:
    56px
    130px
    45px
    85px
    60px
    85px
    150px
    70px
    110px
    60px
    50px
    110px
    100px;
  gap: 0.4rem;
  text-align: center;
}

.table-header {
  font-weight: 700;
  color: #cbd5f5;
  border-bottom: 1px solid #475569;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

/* IMPORTANT: needed for tooltips */
.table-header > div {
  position: relative;
}

/* ===== Header Tooltips ===== */
.has-tooltip {
  cursor: help;
}

.has-tooltip .tooltip {
  position: absolute;
  bottom: 130%;
  left: 50%;
  transform: translateX(-50%);

  background: var(--bg-dark);
  color: #e5e7eb;
  border: 1px solid #475569;
  border-radius: 0.5rem;
  padding: 0.5rem 0.75rem;

  font-size: 0.75rem;
  line-height: 1.2;
  width: 220px;
  text-align: center;

  opacity: 0;
  pointer-events: none;
  transition: opacity 0.15s ease;
  z-index: 9999;
}

.has-tooltip:hover .tooltip {
  opacity: 1;
}

/* ===== Picture Column ===== */
.picture-cell {
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  left: 0;
  background: var(--bg-dark);
  z-index: 2;
}

.table-header .picture-cell {
  z-index: 3;
}

.table-avatar {
  width: 40px;
  height: 40px;
  border-radius: 0.5rem;
  object-fit: cover;
  border: 1px solid #475569;
}

/* ===== Tiles ===== */
.tile {
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 1px solid #475569;
  font-size: 0.875rem;
}

.tile.correct {
  background: var(--green);
  border-color: var(--green-soft);
  color: white;
}

.tile.close {
  background: var(--gold);
  border-color: var(--gold-soft);
  color: #020617;
}

.tile.wrong {
  background: var(--bg-dark);
  color: #e5e7eb;
}

/* ===== Autocomplete ===== */
.autocomplete {
  position: relative;
}

.dropdown {
  position: absolute;
  top: 110%;
  width: 100%;
  background: var(--bg-dark);
  border: 1px solid #475569;
  border-radius: 0.5rem;
  max-height: 220px;
  overflow-y: auto;
  z-index: 20;
  list-style: none;
  padding: 0;
  margin: 0;
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.5rem 0.75rem;
  cursor: pointer;
}

.dropdown-item:hover,
.dropdown-item.active {
  background: rgba(212, 175, 55, 0.15);
}

.avatar {
  width: 36px;
  height: 36px;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid #475569;
}

.highlight {
  color: var(--gold-soft);
  font-weight: 700;
}

/* ===== Report Modal ===== */
.report-open-btn {
  margin-top: 1.5rem;
  width: 100%;
  padding: 0.75rem;
  border-radius: 0.75rem;
  background: transparent;
  border: 1px dashed var(--gold);
  color: var(--gold);
  font-weight: 600;
  cursor: pointer;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(2, 6, 23, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.modal {
  width: 100%;
  max-width: 420px;
  background: var(--bg-card);
  border-radius: 1rem;
  padding: 1.25rem;
  border: 1px solid var(--gold);
}

.modal label {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-bottom: 0.75rem;
}

.modal input,
.modal select {
  padding: 0.5rem;
  border-radius: 0.5rem;
  background: var(--bg-dark);
  border: 1px solid #475569;
  color: #e5e7eb;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

/* ===== Mobile ===== */
@media (max-width: 640px) {
  .game-card {
    padding: 1rem;
  }

  .guess-form {
    flex-direction: column;
  }

  .table {
    min-width: 900px;
  }
}